<template>
  <div class="particle-network">
    <canvas ref="canvas"></canvas>
  </div>
</template>

<script>
export default {
name: 'ParticleNetwork',
data() {
  return {
    canvas: null,
    ctx: null,
    particles: [],
    mouse: {
      x: null,
      y: null,
      radius: 150
    }
  }
},
props: {
  particleColor: {
    type: String,
    default: '#333'
  },
  lineColor: {
    type: String,
    default: 'rgba(100,100,100,0.3)'
  },
  particleAmount: {
    type: Number,
    default: 80
  }
},
mounted() {
  this.initCanvas()
  this.initParticles()
  this.animate = this.animate.bind(this)
  this.animate()
  
  window.addEventListener('resize', this.handleResize)
  window.addEventListener('mousemove', this.handleMouseMove)
},
beforeUnmount() {
  window.removeEventListener('resize', this.handleResize)
  window.removeEventListener('mousemove', this.handleMouseMove)
},
methods: {
  initCanvas() {
    this.canvas = this.$refs.canvas
    this.ctx = this.canvas.getContext('2d')
    this.canvas.style.width = '100vw'
    this.canvas.style.height = '100vh'
    this.canvas.width = document.documentElement.clientWidth
    this.canvas.height = document.documentElement.clientHeight
  },

  initParticles() {
    for (let i = 0; i < this.particleAmount; i++) {
      this.particles.push(new Particle(
        this.canvas.width,
        this.canvas.height,
        this.particleColor
      ))
    }
  },

  animate() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height)
    
    this.particles.forEach(particle => {
      particle.update(this.canvas.width, this.canvas.height)
      particle.draw(this.ctx)
      this.connectParticles(particle)
    })

    requestAnimationFrame(this.animate)
  },

  connectParticles(particle) {
    this.particles.forEach(p => {
      const distance = Math.hypot(
        particle.x - p.x,
        particle.y - p.y
      )

      if (distance < 100) {
        this.ctx.beginPath()
        this.ctx.strokeStyle = this.lineColor
        this.ctx.lineWidth = 0.4
        this.ctx.moveTo(particle.x, particle.y)
        this.ctx.lineTo(p.x, p.y)
        this.ctx.stroke()
      }
    })
  },

  handleResize() {
  clearTimeout(this.resizeTimer)
  this.resizeTimer = setTimeout(() => {
      this.canvas.width = document.documentElement.clientWidth
      this.canvas.height = document.documentElement.clientHeight
      this.particles = []
      this.initParticles()
    }, 100)
  },

  handleMouseMove(event) {
    this.mouse.x = event.x
    this.mouse.y = event.y
  }
}
}

class Particle {
constructor(width, height, color) {
  this.x = Math.random() * width
  this.y = Math.random() * height
  this.velocity = {
    x: (Math.random() - 0.5) * 1.5,
    y: (Math.random() - 0.5) * 1.0
  }
  this.radius = Math.random() * 2 + 1
  this.color = color
}

update(width, height) {
  this.x += this.velocity.x
  this.y += this.velocity.y
  
  // Korrekte Kollisionserkennung mit Wänden
  if (this.x < 0 || this.x > width) this.velocity.x *= -1
  if (this.y < 0 || this.y > height) this.velocity.y *= -1
}

draw(ctx) {
  ctx.beginPath()
  ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2)
  ctx.fillStyle = this.color
  ctx.fill()
}
}
</script>

<style scoped>
.particle-network {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1; /* Hintergrundebene */
  pointer-events: none; /* Damit es keine Klicks blockiert */
}

canvas {
  background: #000;
}

</style>
