<template>
  <header class="header">
    <div class="userInfo">
      <img src="@/assets/Elyas4.jpg" alt="Profilbild" class="user-img" />
      <p class="user-name">Elyas Jabari</p>
    </div>

    <div class="hobbies-container">
      <h2>Favoriten</h2>
      <div class="animated-text">
        <span>{{ currentText }}</span><span class="cursor"></span>
      </div>
    </div>

    <div class="content-container">
      <div class="information">
        <h1>Willkommen auf meiner Seite!</h1>
        <p>Softwareentwickler im Bereich Frontend und Backend</p>
        <div class="buttons">
          <button @click="scrollToSection('about')">Über mich</button>
          <button @click="scrollToSection('projects')">Projekte</button>
        </div>
      </div>

      <div class="skills-box">
        <h2>Skills:</h2>
        <ul>
          <li>JavaScript</li>
          <li>React</li>
          <li>Vue.js</li>
          <li>Angular</li>
          <li>Java</li>
          <li>Spring Boot</li>
          <li>HTML</li>
          <li>CSS</li>
          <li>Git</li>
          <li>Docker</li>
          <li>PostgreSQL</li>
          <li>REST-API</li>
        </ul>
      </div>
    </div>
  </header>
</template>

<script>

export default {
  
  data() {
    return {
      hobbies: ['Vuejs', 'React', 'Spring Boot'],
      currentHobbyIndex: 0,
      currentText: '',
      isDeleting: false,
      typingSpeed: 100,
      pauseDuration: 1000
    }
  },
  mounted() {
    this.typeText();
  },
  methods: {
    typeText() {
      const current = this.currentHobbyIndex % this.hobbies.length;
      const fullText = this.hobbies[current];

      if (this.isDeleting) {
        this.currentText = fullText.substring(0, this.currentText.length - 1);
      } else {
        this.currentText = fullText.substring(0, this.currentText.length + 1);
      }

      let typeSpeed = this.typingSpeed;
      
      if (this.isDeleting) {
        typeSpeed /= 2;
      }

      if (!this.isDeleting && this.currentText === fullText) {
        typeSpeed = this.pauseDuration;
        this.isDeleting = true;
      } else if (this.isDeleting && this.currentText === '') {
        this.isDeleting = false;
        this.currentHobbyIndex++;
        typeSpeed = 500;
      }

      setTimeout(() => this.typeText(), typeSpeed);
    },
    scrollToSection(sectionId) {
      const section = document.getElementById(sectionId);
      if (section) {
        section.scrollIntoView({ behavior: "smooth" });
      }
    },
  },
};
</script>

