        <img src="@/assets/Elyas4.jpg" alt="Elyas" class="profile-img" />
      </div>
      <p class="description">{{ description }}</p>
      
      <div class="contact-info">
        <div class="contact-item">
          <i class="fas fa-envelope"></i>
          <a :href="'mailto:' + email">{{ email }}</a>
