</template>

<script>
export default {
  data() {
    return {
      description: `Hallo, ich bin Elyas Jabari, 22 Jahre alt und Softwareentwickler mit einem Schwerpunkt auf Frontend- und Backend-Entwicklung. Meine Leidenschaft liegt in der Erstellung moderner und benutzerfreundlicher Webanwendungen.

Neben der Softwareentwicklung spiele ich gerne Fußball, Schach und interessiere mich für Geschichte. Meine berufliche Laufbahn begann mit einer Ausbildung zum Fachinformatiker für Anwendungsentwicklung, die ich im Januar 2025 erfolgreich abgeschlossen habe. Während dieser Zeit konnte ich wertvolle Erfahrungen in der Entwicklung von Software und Webanwendungen sammeln.

Ich habe mit verschiedenen Programmiersprachen und Technologien gearbeitet, darunter JavaScript, React, Vue.js, Angular, Java, Spring Boot, HTML, CSS, PostgreSQL und Docker. Besonders interessieren mich JavaScript, Vue.js und React. Meine Stärken liegen sowohl im UX/UI-Design als auch in der Backend-Entwicklung.

In meiner Freizeit programmiere ich kleine Projekte, die ich im Projektbereich verlinkt habe – dort sind einige meiner spannendsten Arbeiten zu finden.

Diese Website dient als Portfolio, um meine bisherigen Projekte zu präsentieren und einen Einblick in meine Arbeit zu geben. Falls Fragen bestehen oder Interesse an einem Austausch besteht, stehe ich gerne zur Verfügung! 😊
                    
                    `,
      email: "elyas.jabari@hotmail.com",
      phone: "0152 193 422 52",
      github: "https://github.com/ElyasJabari",
      linkedin: "https://www.linkedin.com/in/elyas-jabari-3b4749342/",
      instagram: "https://www.instagram.com/jabarielyas?igsh=MTAzanhkcWtqdWozcQ=="
    };
  },
  mounted() {
    /*For Animation*/
    const section = this.$refs.aboutSection;
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            section.classList.add("fade-in-visible");
          } else {
            section.classList.remove("fade-in-visible");
          }
        });
      },
      { threshold: 0.1 }
    );

    observer.observe(section);
  },
};
</script>

