<template>
  <div id="projects" class="all-container" ref="allContainer">
    <h1 class="project-title">Projekte</h1>
    
    <div class="information">
        <p>Manche Projekte stammen von Anfang meiner Ausbildung, die ich bewusst nicht refaktorisiert habe, weil ich sie gerne als Erinnerung behalten möchte.</p>
    </div>

    <div class="section">
      <h3 class="section-subtitle">Tech Stack - Projekte</h3>
      <div class="scroll-wrapper">
        <button class="scroll-btn left" @click="scrollLeft">‹</button>
        <div class="projects-container" ref="scrollContainer">
          <div class="project-box"
            v-for="project in projects"
            :key="project.name"
            @click="openGithub(project.githubUrl)"
            @mouseover="hoveredProject = project"
            @mouseleave="hoveredProject = null"
            @touchstart="hoveredProject = project"
          >
            <div class="project-info">
              <h3>{{ project.name }}</h3>
              <div class="technologies">
                <span v-for="tech in project.technologies" :key="tech" class="tech-tag">
                  {{ tech }}
                </span>
              </div>
            </div>
            <div v-if="hoveredProject === project" class="project-hover">
              <img :src="project.imageUrl" :alt="project.name + ' preview'" />
            </div>
            <button 
              v-if="project.previewUrl" 
              class="preview-btn"
              @click.stop="openPreview(project.previewUrl)"
            >
              Vorschau
            </button>
          </div>
        </div>
        <button class="scroll-btn right" @click="scrollRight">›</button>
      </div>
    </div>

    <div class="section">
      <h3 class="section-subtitle">Java-Projekte</h3>
      <div class="scroll-wrapper">
        <button class="scroll-btn left" @click="scrollLeftJava">‹</button>
        <div class="projects-container" ref="javaScrollContainer">
          <div class="project-box"
            v-for="project in javaProjects"
            :key="project.name"
            @click="openGithub(project.githubUrl)"
            @mouseover="hoveredProject = project"
            @mouseleave="hoveredProject = null"
            @touchstart="hoveredProject = project"
          >
            <div class="project-info">
              <h3>{{ project.name }}</h3>
              <div class="technologies">
                <span v-for="tech in project.technologies" :key="tech" class="tech-tag">
                  {{ tech }}
                </span>
              </div>
            </div>
            <div v-if="hoveredProject === project" class="project-hover">
              <img :src="project.imageUrl" :alt="project.name + ' preview'" />
            </div>
          </div>
        </div>
        <button class="scroll-btn right" @click="scrollRightJava">›</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hoveredProject: null,
      projects: [
        {
          name: "Shopping List",
          technologies: ["React"],
          imageUrl: require("@/assets/ShoppingList.gif"), 
          githubUrl: "https://github.com/ElyasJabari/shopping-list",
          previewUrl: "https://elyasjabari.github.io/shopping-list/",
        },
        {
          name: "CarShop",
          technologies: ["VueJS", "Spring Boot", "PostgreSQL", "Docker", "REST-API"],
          imageUrl: require("@/assets/CarShop.gif"),
          githubUrl: "https://github.com/ElyasJabari/CarShop",
        },
        {
          name: "HairCut-Web",
          technologies: ["HTML", "CSS", "JavaScript"],
          imageUrl: require("@/assets/Friseursalon.gif"),
          githubUrl: "https://github.com/ElyasJabari/my-javascript-projects/tree/main/Haircut-Web/src",
        },
        {
          name: "Online Kitchen",
          technologies: ["HTML", "CSS", "JavaScript"],
          imageUrl: require("@/assets/Online-Kitchen.gif"),
          githubUrl: "https://github.com/ElyasJabari/my-javascript-projects/tree/main/Web_Online-Kitchen",
        },
        {
          name: "To-Do App",
          technologies: ["HTML", "CSS", "JavaScript"],
          imageUrl: require("@/assets/ToDo.gif"),
          githubUrl:
            "https://github.com/ElyasJabari/my-javascript-projects/tree/main/ToDo-App",
        },
        {
          name: "Wheel of Names",
          technologies: ["HTML", "CSS", "JavaScript"],
          imageUrl: require("@/assets/Spin_the_Wheel.gif"),
          githubUrl:
            "https://github.com/ElyasJabari/my-javascript-projects/tree/main/Spin-the-Wheel",
        },
        {
          name: "Text Editor",
          technologies: ["HTML", "CSS", "JavaScript"],
          imageUrl: require("@/assets/Text_Editor.gif"),
          githubUrl:
            "https://github.com/ElyasJabari/my-javascript-projects/tree/main/Text-Editor",
        },
      ],
      javaProjects: [
        {
          name: "Pizza Order (Console)",
          technologies: ["Java"],
          imageUrl: require("@/assets/Pizza_Order.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/Pizza-order/src",
        },
        {
          name: "Randomizer (JFrame)",
          technologies: ["Java"],
          imageUrl: require("@/assets/Zufallsgenerator.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/Zufallsgenerator/src",
        },
        {
          name: "Bubblesort (Console)",
          technologies: ["Java"],
          imageUrl: require("@/assets/Bubblesort-Algorithmus.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/Bubblesort-Algorithmus/src",
        },
        {
          name: "Calculator App (JFrame)",
          technologies: ["Java"],
          imageUrl: require("@/assets/CalculatorApp.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/Calculator/src",
        },
        {
          name: "IntToRoman  (Console)",
          technologies: ["Java"],
          imageUrl: require("@/assets/IntToRoman.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/IntToRoman/src",
        },
        {
          name: "To-Do App (JFrame)",
          technologies: ["Java"],
          imageUrl: require("@/assets/ToDo_Java.png"),
          githubUrl:
            "https://github.com/ElyasJabari/my-java-projects/tree/main/To-Do-App/src",
        },
      ],
    };
  },
  methods: {
    openPreview(url) {
      window.open(url, '_blank');
    },
    openGithub(url) {
      window.location.href = url;
    },
    scrollLeft() {
      this.$refs.scrollContainer.scrollBy({ left: -300, behavior: "smooth" });
    },
    scrollRight() {
      this.$refs.scrollContainer.scrollBy({ left: 300, behavior: "smooth" });
    },
    scrollLeftJava() {
      this.$refs.javaScrollContainer.scrollBy({
        left: -300,
        behavior: "smooth",
      });
    },
    scrollRightJava() {
      this.$refs.javaScrollContainer.scrollBy({
        left: 300,
        behavior: "smooth",
      });
    },
  },
  mounted() {
    // Ein einzelner Observer für den gesamten Container
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            entry.target.classList.add("fade-in-visible");
          } else {
            entry.target.classList.remove("fade-in-visible");
          }
        });
      },
      { threshold: 0.3 }
    );
    observer.observe(this.$refs.allContainer);
  },
};
</script>

<style scoped>
.all-container {
  width: 90%;
  margin: auto;
  margin-top: 120px;
  margin-bottom: 80px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 20px;
  box-shadow: 0 0 15px rgba(0, 255, 136, 0.2);
  opacity: 0;
  transform: translateY(50px);
  transition: opacity 0.8s ease-out, transform 0.8s ease-out;
}

.fade-in-visible {
  opacity: 1;
  transform: translateY(0);
}

.project-title {
  text-align: center;
  color: rgba(255, 255, 255, 0.8);
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 2rem;
  border-left: 3px solid rgb(0, 158, 102);
  border-right: 3px solid rgb(0, 158, 102);
  border-top-right-radius: 50px;
  border-top-left-radius: 50px;
  padding: 10px 20px;
  letter-spacing: 0.2rem;
  display: inline-block;
}

.section {
  margin-bottom: 3rem;
}

.section-subtitle {
  position: relative;
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 1rem;
  text-align: left;
  margin-left: 45px;
}

.section-subtitle::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  width: 18%;
  height: 2px;
  background: linear-gradient(90deg, #00ff88, transparent);
}

.scroll-wrapper {
  position: relative;
}

.projects-container {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 40px;
  white-space: nowrap;
  scrollbar-width: none;
}

.projects-container::-webkit-scrollbar {
  display: none;
}

.project-box {
  position: relative;
  width: 300px;
  height: 370px;
  background: #0d0d0d;
  border-radius: 0px 30px 0px 30px;
  box-shadow: 0 0 15px rgba(0, 255, 136, 0.3);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: pointer;
  transition: transform 0.3s, box-shadow 0.3s;
  flex-shrink: 0;
  margin: 10px;
}

.project-box:hover {
  transform: scale(1.05);
  box-shadow: 0 0 25px rgba(0, 255, 136, 0.7);
}

.project-info h3 {
  color: rgba(255, 255, 255, 0.8);
  font-weight: bold;
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.technologies {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.tech-tag {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.8);
  padding: 5px 10px;
  margin: 5px;
  border-radius: 10px;
  font-size: 0.9rem;
}

.preview-btn {
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  background: rgba(0, 158, 102, 0.7);
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 20px;
  cursor: pointer;
  z-index: 10;
  transition: all 0.3s ease;
  font-size: 1rem;
  font-weight: 600;
}

.preview-btn:hover {
  background: rgba(0, 158, 102, 1);
  box-shadow: 0 0 10px rgba(0, 255, 136, 0.7);
}

.project-hover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0px 30px 0px 30px;
  opacity: 0;
  transition: opacity 0.3s ease;
  overflow: hidden;
}

.project-box:hover .project-hover {
  opacity: 1;
}

.project-hover img {
  width: 95%;
  border-radius: 10px;
  animation: slideInFromBottom 0.5s ease forwards;
}

.information {
  background: rgba(255, 255, 255, 0.05);
  color: rgba(255, 255, 255, 0.8);
  padding: 15px 20px;
  border-left: 4px solid rgb(0, 255, 136);
  border-radius: 10px;
  font-size: 1rem;
  line-height: 1.6;
  max-width: 600px;
  margin: 20px auto;
  box-shadow: 0 0 10px rgba(0, 255, 136, 0.2);
  text-align: center;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}




@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.scroll-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: none;
  font-size: 2rem;
  cursor: pointer;
  padding: 10px 15px;
  border-radius: 50%;
  transition: background 0.3s;
}

.scroll-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  box-shadow: 0 4px 15px rgba(0, 255, 136, 0.2);
}

.left {
  left: -50px;
}

.right {
  right: -50px;
}

@media (max-width: 768px) {
  .scroll-btn {
    display: none;
  }
  .all-container {
    margin-top: 3px;
    padding-bottom: 5px;
  }
  .projects-container {
    padding: 5px;
    margin-top: 10%;
  }
  .section-subtitle {
  margin-left: 12px;
  }
  .section-subtitle::after {
  width: 65%;
  }
  .project-box {
    width: 250px;
    height: 350px;
  }
}
</style>
